import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./App.css";
import { useNavigate } from "react-router-dom";
 import { API_URL } from "./api";


export default function Login() {
  const [nickname, setNickname] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
  const response = await fetch(`${API_URL}/login`, {

        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ nickname, password }),
      });

      const data = await response.json();
      if (response.ok) {
        alert(" Bienvenido " + data.user.nickname);
        localStorage.setItem("usuario", JSON.stringify(data.user));

        // redireccin segn el tipo de usuario
        switch (data.user.tipo) {
          case "natural":
            navigate("/natural");
            break;
          case "medico":
            navigate("/medico");
            break;
          case "enfermera":
            navigate("/enfermera");
            break;
          case "tecnico":
            navigate("/tecnico");
            break;
          case "biomedico":
            navigate("/biomedico");
            break;
          default:
            navigate("/");
        }
      } else {
        alert(" " + data.error);
      }
    } catch (error) {
      alert(" Error de conexin con el servidor");
    }
  };

  return (
    <div className="login-container">
      <h2> Iniciar sesin</h2>
      <form onSubmit={handleLogin}>
        <label htmlFor="username">Usuario:</label>
        <input
          type="text"
          id="username"
          placeholder="Ingresa tu usuario"
          value={nickname}
          onChange={(e) => setNickname(e.target.value)}
          required
        />

        <label htmlFor="password">Contrasea:</label>
        <input
          type="password"
          id="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />

        <button type="submit" className="secondary-btn">
          Entrar
        </button>
      </form>

      <button
        type="button"
        className="secondary-btn"
        onClick={() => navigate("/registro")}
      >
        Registrar
      </button>

      <div className="extra-info">
        <p>
          Olvidaste tu contrasea? <Link to="/recuperar">Recuperar</Link>
        </p>
      </div>
    </div>
  );
}
